<template>
  <div class="message-alert">
    <div class="alert alert-dismissible "
      :class="'alert-' + item.status" style="z-index:9999"
      v-for="(item, i) in messages" :key="i">
      <i class="fas fa-heart" v-if="item.message ===''"></i>
      <i class="fas fa-heart-broken" v-else-if="item.message === ''"></i>
      {{ item.message }}
      <button type="button" class="close" @click="removeMessage(i)" aria-label="Close">
        <span aria-hidden="true">&times;</span>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Navbar',
  data () {
    return {
      messages: []
    }
  },
  methods: {
    updateMessage (message, status) {
      const timestamp = Math.floor(new Date() / 1000)
      this.messages.push({
        message,
        status,
        timestamp
      })
      this.removeMessageWithTiming(timestamp)
    },
    removeMessage (num) {
      this.messages.splice(num, 1)
    },
    removeMessageWithTiming (timestamp) {
      const vm = this
      setTimeout(() => {
        vm.messages.forEach((item, i) => {
          if (item.timestamp === timestamp) {
            vm.messages.splice(i, 1)
          }
        })
      }, 1500)
    }
  },
  created () {
    const vm = this
    //  'messsage:push'
    // message: 
    // status:  warning  BS4
    // $on 
    vm.$bus.$on('messsage:push', (message, status = 'warning') => {
      vm.updateMessage(message, status) // 
    })
    vm.$bus.$on('like', () => {
      vm.removeMessage()
      vm.updateMessage('', 'primary')
    })
    vm.$bus.$on('dislike', () => {
      vm.removeMessage()
      vm.updateMessage('', 'info')
    })
  }
}
</script>

<style scope>
.message-alert {
  position: fixed;
  top: 40%;
  left: 43%;
  z-index: 1100;
  font-size: 22px;
}
.alert-dismissible{
  font-weight: bold;
}
</style>
